/**
 * @returns {Promise<Array<chrome.management.ExtensionInfo>>}
 */
export const getExtensionInfos = () => {
  return new Promise(resolve => {
    chrome.management.getAll(extensionInfos => {
      resolve(extensionInfos);
    });
  });
};

const setExtensionsEnableStatus = (id, enable = false) => {
  return new Promise(resolve => {
    browser.management.setEnabled(id, enable, () => {
      resolve();
    });
  });
};

export const getActiveProxyExtensions = async () => {
  const currentExtensionId = sdk.config.application.applicationId;
  const extensions = await getExtensionInfos();
  return extensions.filter(({ enabled, id, permissions, type, mayDisable, name }) => {
    if (type !== 'extension') return false;
    if (id === currentExtensionId) return false;
    if (!enabled) return false;
    if (!mayDisable) return false;
    if (name.toLowerCase().includes('expressvpn'.toLowerCase())) return true;
    return permissions.includes('proxy');
  });
};

export const hasOtherProxyExtensions = async () => {
  const extensions = await getActiveProxyExtensions();
  return extensions.length > 0;
};

export const disableOtherProxyExtensions = async () => {
  const extensions = await getActiveProxyExtensions();
  for (const { id } of extensions) {
    // eslint-disable-next-line no-await-in-loop
    await setExtensionsEnableStatus(id, false).catch(() => 0);
  }
};
