import React from 'react';
import './footer.less';
import classNames from 'classnames';
import {
  MainIconSvg,
  MainActiveIconSvg,
  CouponIconSvg,
  CouponActiveIconSvg,
  MenuIconSvg,
  MenuActiveIconSvg
} from '@betternet/shared/icons';

function Footer({ screen, onSelect }) {
  const isMenu = screen?.toLowerCase() === 'menu';
  const isCoupons = screen?.toLowerCase() === 'coupons';
  const isMain = screen?.toLowerCase() === 'main' || !screen;
  return (
    <footer className='footer'>
      <ul className='footer__row'>
        <li
          className={classNames('footer__item ', { 'footer__item--active': isMain })}
          onClick={() => onSelect('main')}
        >
          {isMain ? <MainActiveIconSvg className='footer__icon' /> : <MainIconSvg className='footer__icon' />}
        </li>
        <li
          className={classNames('footer__item ', { 'footer__item--active': isCoupons })}
          onClick={() => onSelect('coupons')}
        >
          {isCoupons ? <CouponActiveIconSvg className='footer__icon' /> : <CouponIconSvg className='footer__icon' />}
        </li>
        <li
          className={classNames('footer__item ', { 'footer__item--active': isMenu })}
          onClick={() => onSelect('menu')}
        >
          {isMenu ? <MenuActiveIconSvg className='footer__icon' /> : <MenuIconSvg className='footer__icon' />}
        </li>
      </ul>
    </footer>
  );
}

export default Footer;
