import './Unavailable.less';
import React from 'react';
import { t } from '@common-shared/Translation';

function Unavailable() {
  const click = () => {
    sdk.reporting.report('click', 'unavailable');
    sdk.proxy.disableOtherProxy().then(() => panel.showScreen());
  };

  return (
    <div id='screenUnavailable'>
      <div className='title'>{t('unavailable_title')}</div>
      <div className='description'>{t('unavailable_description', { browser: sdk.config.browser })}</div>
      <div className='button blueButton' onClick={click}>
        {t('unavailable_button')}
      </div>
    </div>
  );
}

export default Unavailable;
