import React, { useCallback, useEffect, useState } from 'react';
import OfferList from '@betternet/shared/panel/components/shop-details/offer-list';
import BetternetLogo from '@betternet/shared/ui/components/Image/betternet-logo';
import MerchantList from '@betternet/shared/panel/components/shop-details/merchant-list';
import GettingStartedView from '@betternet/shared/panel/components/shop-details/getting-started-view';
import './merchant-view.less';
import { useAcceptSafeShoppingFeature } from '@betternet/shared/hooks/safe-shoppiing-feature';
import { getActiveTab } from '@softomate/rework-party';
import Button from '@betternet/shared/ui/components/button';
import classNames from 'classnames';
import { sendContentInternalAnalytics, sendContentReport } from '@common-shared/Reporting/reporting-methods';

function useGetCaaStatus() {
  const [status, setStatus] = useState(false);
  const checkCAAStatus = useCallback(async () => {
    const tab = await getActiveTab();
    if (!tab?.id) return;
    browser.tabs.sendMessage(tab.id, { message: 'get_caa_status' }, response => {
      if (response?.status === 'init') {
        setStatus(true);
      } else {
        setStatus(false);
      }
    });
  }, []);
  useEffect(() => {
    checkCAAStatus();
    const interval = setInterval(() => {
      checkCAAStatus();
    }, 3000);
    return () => clearInterval(interval);
  }, []);

  const init = useCallback(async () => {
    const tab = await getActiveTab();
    if (!tab?.id) return;
    browser.tabs.sendMessage(tab.id, { message: 'start_caa' });
  }, []);

  return [!!status, init];
}

function MerchantView({ merchant, merchants = [] }) {
  const [isCaaReady, initCAA] = useGetCaaStatus();
  const { isShoppingFeatureAccepted, onShoppingFeatureAccept } = useAcceptSafeShoppingFeature();

  useEffect(() => {
    sendContentReport('safe_shopping_tab_coupons_list', 'shown', merchant?.domain);
    sendContentInternalAnalytics('ui_view', { view_name: 'scn_coupons' });
  }, []);

  if (isShoppingFeatureAccepted === false) {
    return <GettingStartedView close={onShoppingFeatureAccept} domain={merchant?.domain} />;
  }

  const image = merchant?.image ?? '/static/assets/elements/coupons/default-merchant-icon.svg';
  return (
    <section className='merchant'>
      <div className='merchant-header'>
        <div className={classNames('merchant-header__content', { 'merchant-header__content--with-caa': isCaaReady })}>
          <div className='merchant-header__info'>
            <h1>
              <BetternetLogo />
            </h1>
            <p className='merchant-header__title'>
              {merchant?.name || 'Stay safe while you are shopping and get the discounts from your favorite shops'}
            </p>
            <div className='merchant-header__cashback' />
          </div>
          <div className='merchant-header__logo' style={{ backgroundImage: `url(${image})` }} />
          <div className='merchant-header__sparkles' />
          {isCaaReady && (
            <div className='merchant-header__caa-button'>
              <Button onClick={initCAA} blue fullWidth>
                Apply coupons
              </Button>
            </div>
          )}
        </div>
      </div>
      {merchant && (
        <OfferList
          offers={merchant.offers}
          name={merchant.name}
          domain={merchant.domain}
          gaPrefix='safe_shopping_tab_'
          internalPrefix='scn_'
        />
      )}
      {!merchant && <MerchantList merchants={merchants} />}
    </section>
  );
}

export default MerchantView;
