import './Locations.less';

import React, { Component } from 'react';
import classnames from 'classnames';
import countryData from 'country-data';
import CountryFlag from '@betternet/shared/panel/components/country-flag/country-flag';
import { PremiumLocationSvg, CheckedCircleSvg, CloseIconSvg } from '@betternet/shared/icons';
import Button from '@betternet/shared/ui/components/button';
import { WantToGoFasterBanner } from '@betternet/shared/panel/components';

const LAST_LOCATION = 'Panel.lastLocation';

class Locations extends Component {
  changeLocation = location => {
    /* if(!sdk.user.status.elite) {
      return sdk.user.openPopUp('location_'+location);
    } */

    if (location !== storage.location) {
      sdk.storage.set(LAST_LOCATION, location);
    }

    storage.location = location;

    if (sdk.proxy.status.status === 'connected') {
      sdk.reporting.report('connection', 'reconnect');
      sdk.proxy.connect(location, true).then(() => {
        controller.setState({ screen: 'main' });
      });
    } else {
      controller.setState({ screen: 'main' });
    }
  };

  render() {
    const locations = [];
    const isPremium = !!sdk.user.status.elite;

    const list = sdk.proxy.getLocations(isPremium ? 'elite' : undefined);
    for (const k of list) {
      locations.push(
        <Button
          fullWidth
          transparent
          spaceBetween
          className='locations__item'
          key={k}
          onClick={() => this.changeLocation(k)}
        >
          <CountryFlag location={k} />
          <span>{countryData.countries[k.toUpperCase()].name}</span>
          {storage.location === k ? <CheckedCircleSvg /> : null}
        </Button>
      );
    }

    if (!isPremium) {
      const elite = sdk.proxy.getLocations('elite');
      for (const k of elite) {
        if (list.includes(k)) continue;
        locations.push(
          <Button
            fullWidth
            transparent
            spaceBetween
            className='locations__item'
            key={k}
            onClick={() => sdk.user.openPopUp('main_promo')}
          >
            <CountryFlag location={k} />
            <span>{countryData.countries[k.toUpperCase()].name}</span>
            <PremiumLocationSvg />
          </Button>
        );
      }
    }

    return (
      <div className='locations'>
        <div
          className='locations__header'
          onClick={() => {
            controller.setState({ screen: 'main' });
          }}
        >
          <CloseIconSvg />
        </div>
        <div className={classnames('locations__container', { 'locations__container--premium': isPremium })}>
          <h1 className='locations__title'>Virtual locations</h1>
          <div className='locations__optional-group'>
            <Button
              fullWidth
              transparent
              spaceBetween
              className='locations__item'
              onClick={() => this.changeLocation('optimal')}
            >
              <CountryFlag location='us' />
              <span>{!isPremium ? 'Limited speed' : 'Best connection'}</span>
              {storage.location === 'optimal' ? <CheckedCircleSvg /> : null}
            </Button>
            {!isPremium && <WantToGoFasterBanner />}
          </div>
          <div className='locations__list'>{locations}</div>
        </div>
        {!isPremium && (
          <div className='locations__go-premium' onClick={() => sdk.user.openPopUp('main_promo')}>
            <h4>Go Premium</h4>
            <p>To access all locations</p>
          </div>
        )}
      </div>
    );
  }
}

export default Locations;
