import React from 'react';
import './merchant-card.less';
import { popupActivate } from '@betternet/shared/panel/components/shop-details/popup-activation';

function MerchantCard({ merchant }) {
  return (
    <article className='merchant-card' onClick={() => popupActivate(merchant)}>
      <p className='merchant-card__discount'>{merchant.discount}</p>
      <p className='merchant-card__description'>{merchant.name}</p>
      <div className='merchant-card__logo' style={{ backgroundImage: `url(${merchant.image})` }} />
    </article>
  );
}

export default MerchantCard;
