import React from 'react';
import countryData from 'country-data';
import { t } from '@common-shared/Translation';
import './Location.less';
import Button from '@betternet/shared/ui/components/button';
import { DropdownSvg } from '@betternet/shared/icons';
import CountryFlag from '@betternet/shared/panel/components/country-flag/country-flag';

function Location() {
  const location = storage.location;
  const toLocations = () => controller.setState({ screen: 'locations' });
  let text;
  if (location === 'optimal') {
    text = !sdk.user.status.elite ? 'Limited speed' : 'Best connection';
  } else {
    text = countryData.countries[location.toUpperCase()].name;
  }

  return (
    <Button fullWidth spaceBetween lightGrey onClick={toLocations}>
      <CountryFlag location={location} />
      {text}
      <DropdownSvg />
    </Button>
  );
}

export default Location;
