import React, { Component } from 'react';
import MerchantView from '@betternet/shared/panel/components/shop-details/merchant-view';
import Footer from '@betternet/shared/panel/components/footer';
import { hasOtherProxyExtensions } from '@common-shared/Utils/ExtensionManagment';
import { isFirefox } from '@common-shared/BrowserDetection';
import Main from './screens/Main';
import Menu from './screens/Menu';
import Loading from './screens/Loading';
import Locations from './screens/Locations';
import Unavailable from './screens/Unavailable';

class Controller extends Component {
  constructor(props) {
    super(props);

    this.state = {
      screen: 'loading',
      merchant: null,
      merchants: []
    };
  }

  async init() {
    if (!sdk.coupons?.merchants) {
      setTimeout(() => this.init(), 50);
    } else {
      try {
        const isUnavailable = await hasOtherProxyExtensions();
        if (!isFirefox && isUnavailable) {
          this.setState({ screen: 'unavailable' });
          return;
        }
        const merchant = await sdk.coupons.merchants.getCurrentMerchant();
        this.setState({ merchant });
        if (sdk.proxy.status.status === 'connected' || !merchant) {
          this.setState({ merchants: sdk.coupons.topMerchants.data ?? [], screen: 'main' });
        } else {
          this.setState({ screen: 'coupons' });
        }
      } catch {
        this.setState({ screen: 'main' });
      }
    }
  }

  componentDidMount() {
    this.init();
    setTimeout(() => {
      // if (this.state.screen === 'loading') this.setState({ screen: 'main' });
    }, 2500);
  }

  render() {
    let content = null;

    switch (this.state.screen) {
      case 'loading':
        content = <Loading screen={this.state.screen} />;
        break;
      case 'locations':
        content = <Locations screen={this.state.screen} />;
        break;
      case 'menu':
        content = (
          <div className='root_container'>
            <Menu screen={this.state.screen} />
            <Footer screen={this.state.screen} onSelect={screen => this.setState({ screen })} />
          </div>
        );
        break;
      case 'unavailable':
        content = <Unavailable screen={this.state.screen} />;
        break;
      case 'main':
        content = (
          <div className='root_container'>
            <Main screen={this.state.screen} />
            <Footer screen={this.state.screen} onSelect={screen => this.setState({ screen })} />
          </div>
        );
        break;
      case 'coupons':
        content = (
          <div className='root_container'>
            <MerchantView merchant={this.state.merchant} merchants={this.state.merchants} />
            <Footer screen={this.state.screen} onSelect={screen => this.setState({ screen })} />
          </div>
        );
        break;
      default:
        content = (
          <div className='root_container'>
            <Main screen='main' />
            <Footer screen={this.state.screen} onSelect={screen => this.setState({ screen })} />
          </div>
        );
    }

    return content;
  }
}

export default Controller;
