import React from 'react';
import MerchantCard from '@betternet/shared/panel/components/shop-details/merchant-card';
import './merchant-list.less';

function MerchantList({ merchants = [] }) {
  return (
    <section className='merchant-list'>
      <div className='merchant-list__title'>Best stores today</div>
      {merchants.map(merchant => (
        <MerchantCard merchant={merchant} key={merchant.id} />
      ))}
    </section>
  );
}

export default MerchantList;
