import './input.less';

import React, { useCallback, useEffect, useState } from 'react';
import { useBoolean } from 'react-use';
import classNames from 'classnames';

function Input(props) {
  const [focus, setFocus] = useBoolean(false);
  const [value, setValue] = useState(props.value);
  useEffect(() => {
    setValue(props.value);
  }, [props.value]);

  const onFocus = useCallback(() => {
    props.onFocus?.();
    setFocus(true);
  }, [props.onFocus]);

  const onBlur = useCallback(() => {
    props.onBlur?.();
    setFocus(false);
  }, [props.onBlur]);

  const onChange = useCallback(
    event => {
      const eventValue = event.target.value;
      props.onChange?.(eventValue);
      setValue(eventValue);
    },
    [props.onChange]
  );

  const onEnter = useCallback(
    event => {
      if (event.key !== 'Enter' || props.disabled) {
        return;
      }
      props.onEnter?.();
    },
    [props.onEnter, props.disabled]
  );

  const wrapperClass = classNames('input', props.theme || 'default', {
    [props.className]: props.className,
    notValid: props.notValid,
    disabled: props.disabled,
    focus
  });
  const labelClass = classNames('label', { hidden: focus || value !== '' });

  return (
    <div className={wrapperClass}>
      {props.label && <div className={labelClass}>{props.label}</div>}
      <input
        type={props.type || 'text'}
        value={value}
        onBlur={onBlur}
        onFocus={onFocus}
        onChange={onChange}
        onKeyPress={onEnter}
        disabled={props.disabled ? 'disabled' : ''}
      />
      {props.message && <div className='inputMessage'>{props.message}</div>}
    </div>
  );
}

export default Input;
