import './Main.less';
import React, { Component } from 'react';

import Face from '@betternet/shared/panel/components/face';
import { t } from '@common-shared/Translation';
import { asyncTimeout } from '@common-shared/Utils/AsyncTimeout';
import LimitedTimeOffer from '@betternet/shared/panel/components/banners/limited-time-offer';
import { sendContentMessage } from '@softomate/rework-party';
import Button from '@betternet/shared/ui/components/button';
import { WantToGoFasterBanner } from '@betternet/shared/panel/components';
import Header from './Header';
import Location from './Location';

class Main extends Component {
  constructor(props) {
    super(props);

    this.state = {
      status: sdk.proxy.status.status,
      connecting: false,
      limitedTimeOffer: undefined
    };
  }

  connect = async () => {
    this.setState({ connecting: true });

    sdk.reporting.report('connection', 'connecting');
    try {
      await sdk.proxy.connect(storage.location);
      await sdk.reporting.report('connection', 'connect');

      await asyncTimeout(1000);
      if (this.state.connecting) {
        this.setState({ status: 'connected', connecting: false });
      } else if (sdk.proxy.status.status !== 'disconnected') {
        await this.disconnect();
      }
    } catch (e) {
      error('[MainComponent] fail on proxy connect', e);
    }
  };

  disconnect = () => {
    sdk.reporting.report('connection', 'disconnect');
    sdk.proxy.disconnect().then(() => {
      this.setState({ status: 'disconnected' });
    });
  };

  cancelConnect = () => {
    sdk.reporting.report('connection', 'cancel');
    this.setState({ connecting: false });
    sdk.proxy.disconnect({ cancel: true }).then(() => {
      this.setState({ status: 'disconnected' });
    });
  };

  async componentDidMount() {
    const limitedTimeOffer = await sendContentMessage({ method: 'limitedTimeOffer.getTime' });
    this.setState({ limitedTimeOffer });
  }

  render() {
    const limitedTimeOfferIsExpired = !this.state.limitedTimeOffer?.isExpired;
    const showLimitedTimeOffer =
      limitedTimeOfferIsExpired && this.state.status === 'connected' && !sdk.user.status.elite;
    let button;
    if (this.state.connecting) {
      button = (
        <Button fullWidth blue onClick={this.cancelConnect}>
          Cancel
        </Button>
      );
    } else if (this.state.status === 'disconnected') {
      button = (
        <Button fullWidth blue onClick={this.connect}>
          {t('connect_button')}
        </Button>
      );
    } else if (this.state.status === 'connected') {
      button = (
        <Button fullWidth blue onClick={this.disconnect}>
          {t('disconnect_button')}
        </Button>
      );
    }

    let face = 1;

    if (sdk.user.status.elite) face = 4;
    else if (this.state.status === 'connected') face = 3;
    else if (this.state.status === 'connecting') face = 2;

    return (
      <div id='screenMain'>
        <Header />
        <Face
          theme={this.state.status === 'disconnected' ? 'gray' : 'blue'}
          animated={this.state.connecting}
          face={face}
        />
        <div className='main__button-group'>
          {button}
          <Location />
          <WantToGoFasterBanner />
        </div>
        <div className='main__footer'>
          {showLimitedTimeOffer && <LimitedTimeOffer endDate={this.state.limitedTimeOffer?.endDate} />}
          {/* eslint-disable-next-line jsx-a11y/accessible-emoji */}
        </div>
      </div>
    );
  }
}

export default Main;
